<template>
  <div class="TimeClockOn">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-img
        :src="require('@/assets/clockon_backgroupd.png')"
        height="100vh"
        class="d-flex align-center"
      >
        <v-row class="justify-center text-center">
          <v-col cols="11">
            <v-alert outlined class="blue lighten-3" style="opacity: 0.8">
              <div v-if="list.type == 'clockOnPage'">
                <div v-if="userData != null">
                  <h2>賀發不銹鋼實業有限公司</h2>
                  <h3>-打卡功能-</h3>
                  <v-divider></v-divider>
                  <h2>
                    日期:{{ nowDay }} <br />
                    時間:{{ nowTime }}
                  </h2>
                  <v-divider class="my-2"></v-divider>

                  <div class="d-flex justify-center font-weight-bold">
                    <div
                      class="white rounded mr-2"
                      v-ripple
                      @click="confirmClock('上班')"
                    >
                      <img
                        :src="
                          require('@/assets/img/shigotohajime_woman_good.png')
                        "
                        alt=""
                        style="width: 100%"
                      />
                      <div>上班打卡</div>
                    </div>

                    <div
                      class="white rounded"
                      v-ripple
                      @click="confirmClock('下班')"
                    >
                      <img
                        :src="require('@/assets/img/kitaku_family.png')"
                        alt=""
                        style="width: 100%"
                        class="rounded"
                      />
                      <div>下班打卡</div>
                    </div>
                  </div>
                </div>
                <div v-else>
                  <h1>
                    未綁定任何資料!! <br />
                    請洽公司管理員
                  </h1>
                </div>
              </div>

              <div v-else>
                <div v-if="list.type == 'linkQrcode'">
                  <h1>打卡帳號綁定!!</h1>
                  <v-divider />
                  <h2>綁定人員->{{ list.name }}</h2>
                </div>
                <div v-else>
                  <h1>取消綁定!!</h1>
                </div>
              </div>

              <!-- <pre>{{timecardItems}}</pre> -->
            </v-alert>
          </v-col>
        </v-row>
      </v-img>
    </v-card>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      list: JSON.parse(this.$route.query.data),
      cData: JSON.parse(sessionStorage.getItem("cData")),
      userData: JSON.parse(localStorage.getItem("herfaUserQr")),
      // timecardItems:[],//打卡資料暫存

      //打卡鐘
      nowDay: dayjs().format("YYYY-MM-DD"),
      nowTime: dayjs().format("HH:mm:ss"),
    };
  },

  mounted() {
    if (this.list.type == "clockOnPage") {
      //打卡
      this.showTime();
      // this.getData("timecard");
    } else {
      //綁定/取消綁定
      if (this.list.type == "linkQrcode") {
        localStorage.setItem("herfaUserQr", this.$route.query.data);
      } else {
        localStorage.removeItem("herfaUserQr");
      }
    }
  },

  methods: {
    //取得指定資料
    getData(tableName) {
      this.myAxios(
        "general/getAll/" + this.$store.state.databaseName + "/" + tableName
      ).then((rs) => {
        // console.log(tableName,rs)
        this[tableName + "Items"] = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    //打卡確認
    confirmClock(workType) {
      this.yn("確認[" + workType + "]打卡").then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          // 判斷重覆打卡
          this.myAxios(
            "general/getAll/" + this.$store.state.databaseName + "/timecard"
          ).then((rs) => {
            let timecardItems = rs.map((i) => {
              let newItem = JSON.parse(i.datalist);
              newItem.snkey = i.snkey;
              if (newItem.picname == undefined) {
                newItem.picname = "lazypic.jpg";
              }
              return newItem;
            });
            let match = timecardItems.find(i=>{
              return (
                i.date == this.nowDay &&
                i.personnel.snkey == this.list.snkey &&
                i.workType == workType
              )
            })

            if (match){
              this.$swal({ icon: "warning", text: "重覆打卡!請再確認!!" });
            }else{
              this.postData(workType)
            }
          });
        }
      });
    },
    postData(workType) {
      let clockData = {
        date: this.nowDay,
        time: this.nowTime,
        personnel: {
          text: this.list.name,
          snkey: this.list.snkey,
        },
        workType: workType,
        create_man: this.$store.state.pData.name + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"
      };

      let postData = {
        datalist: JSON.stringify(clockData),
      };
      this.myAxios(
        "general/add/" + this.$store.state.databaseName + "/timecard",
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          this.$swal({ icon: "success", text: workType+"打卡成功!" });
        }
      });
    },
    showTime() {
      var intervalID = setInterval(() => {
        this.nowDay = dayjs().format("YYYY-MM-DD");
        this.nowTime = dayjs().format("HH:mm:ss");
      }, 1000);
    },
  },
};
</script>